.popular{
    display: flex;
    flex-direction: column;
    align-items: center;
    align-content:center ;
    gap:10px;
    height:90vh;
}

.popular h1{
    color:#171717;
    font-size:50px;
    font-weight:600;
}
.popular_hr{
    width:200px;
    height:6px;
    border-radius: 10px;
    background: #252525;
}
.popular-item{
    margin-top: 50px;
    display: flex;
    gap:30px;
}